<template>
  <!-- begin:: Page -->
  <div>
    <div style="padding:10px 0">
      <span class="title">定位信息</span>
      <van-button
        plain
        size="mini"
        type="info"
        style="float:right;margin-right:10px"
        icon="location-o"
        @click="getLocation()"
      >重新定位</van-button>
    </div>
    <van-cell-group>
      <van-cell title="经度">
        <template #default>
          <span>{{ latitude }}</span>
        </template>
      </van-cell>
      <van-cell title="纬度">
        <template #default>
          <span>{{ longitude }}</span>
        </template>
      </van-cell>
      <van-cell v-if="showErr" title="错误">
        <template #default>
          <span>{{ err }}</span>
        </template>
      </van-cell>
    </van-cell-group>
    <!-- 检测定位状态 确定是否可扫码签到 -->
    <div style="margin: 16px;">
      <van-button round block type="info" 
        :loading="!enterRange"
        loading-text="定位中..."
        @click="wxScanCode()">扫码签到</van-button>
    </div>
    <van-cell-group>
      <van-cell title="扫码结果">
        <template #default>
          <span>{{ result }}</span>
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
//  import wx from 'weixin-js-sdk'; //这个是坑
import wx from "weixin-jsapi"; //es6版本的js-sdk
export default {
  name: "Scan",
  components: {},
  data() {
    return {
      config: null,
      latitude: "",
      longitude: "",
      enterRange: false, // 判断是否进入签到范围
      locationSuccess: false, // 判断是否定位成功
      result: "" // 扫码结果
    };
  },
  mounted() {
    this.getLocation()
  },
  methods: {
    getLocation() {
      this.locationSuccess = false;
      this.enterRange = false;
      this.latitude = ''
      this.longitude = ''
      // 获取位置信息后请求后台接口
      this.$axios({
        method: "post",
        url:"/proxy/raptorCustomerServer/service/weChatPublicNumber/test/getTicketSignature.do",
        data: { url: location.href.split("#")[0] } //向服务端提供授权url参数，并且不需要#后面的部分
      })
      .then(res => {
        if (res) {
          let config = res.data.data;
          console.log(config);
          // 存储config 同一个url仅需调用一次
          this.config = config;
          this.$handleGetLocation(config)
            .then(res => {
              console.warn("首页获取位置成功", res);
              this.latitude = res.lat;
              this.longitude = res.lng;
              this.locationSuccess = true
              // 这里根据获取到的经纬度请求后台接口...
              // TODO 判断范围是否正确
              this.enterRange = true;
            })
            .catch(err => {
              console.error("获取定位失败", err);
            });
        }
      })
      .catch(err => {
        console.error("获取定位失败", err);
      });
    },
    wxScanCode() {
      this.$handleScanQR(this.config)
        .then(res => {
          this.result = res.str;
        })
        .catch(err => {
          console.error("扫描二维码失败", err);
        });
      // this.$axios({
      //   method: "post",
      //   url:
      //     "/proxy/raptorCustomerServer/service/weChatPublicNumber/test/getTicketSignature.do",
      //   data: { url: location.href.split("#")[0] } //向服务端提供授权url参数，并且不需要#后面的部分
      // }).then(res => {
      //   let config = res.data.data;
      //   console.log(config);
      //   wx.config({
      //     debug: false, // 开启调试模式,
      //     appId: config.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
      //     timestamp: config.timestamp, // 必填，生成签名的时间戳
      //     nonceStr: config.nonceStr, // 必填，生成签名的随机串
      //     signature: config.signature, // 必填，签名，见附录1
      //     jsApiList: ["scanQRCode"] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
      //   });

      //   wx.ready(function() {
      //     // config信息验证成功后会执行ready方法,所有接口调用都必须在config接口获得结果之后
      //     // config 是一个客户端的异步操作,所以如果需要在页面加载时调用相关接口,则须把相关接口放在ready函数中调用来确保正确执行.对于用户触发是才调用的接口,则可以直接调用,不需要放在ready函数中
      //     wx.checkJsApi({
      //       // 判断当前客户端版本是否支持指定JS接口
      //       jsApiList: ["scanQRCode"],
      //       success: function(res) {
      //         // 以键值对的形式返回，可用true，不可用false。如：{"checkResult":{"scanQRCode":true},"errMsg":"checkJsApi:ok"}
      //         if (res.checkResult.scanQRCode === true) {
      //           wx.scanQRCode({
      //             // 微信扫一扫接口
      //             desc: "scanQRCode desc",
      //             needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
      //             scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
      //             success: function(res) {
      //               var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
      //               // location.href=res.resultStr;//扫描结果传递到的处理页面,跳转到这个页面
      //               this.data.result = res;
      //               // location.href=res.resultStr;//扫描结果传递到的处理页面,跳转到这个页面
      //               // sessionStorage.setItem('saomiao_result',result);
      //               //其它网页调用二维码扫描结果：
      //               // var result = sessionStorage.getItem("saomiao_result");
      //             }
      //           });
      //         } else {
      //           alert("抱歉，当前客户端版本不支持扫一扫");
      //         }
      //       },
      //       fail: function(res) {
      //         // 检测getNetworkType该功能失败时处理
      //         alert("fail" + res);
      //       }
      //     });
      //   });

      //   /* 处理失败验证 */
      //   wx.error(function(res) {
      //     // config 信息验证失败会执行error函数,如签名过期导致验证失败,具体错误信息可以打开config的debug模式查看,也可以在返回的res参数中查看,对于SPA可以在这里更新签名
      //     alert("配置验证失败: " + res.errMsg);
      //   });
      // });
    }
  }
};
</script>
<style scoped>
h2,
.title {
  margin: 0;
  text-align: left;
  padding: 20px 16px 16px;
  color: rgba(69, 90, 100, 0.6);
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
}
</style>



