import wx from "weixin-jsapi";

/**
 * 注入权限验证配置
 * @param {object} 微信 js-sdk 权限验证配置
 */
export const wxconfigInfo = config => {
  wx.config({
    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
    appId: config.appId,
    timestamp: parseInt(config.timestamp),
    nonceStr: config.nonceStr,
    signature: config.signature,
    jsApiList: [
      // 需要使用的 jsapi 列表
      "scanQRCode", // 扫描二维码
      "getLocation" // 获取地理位置
    ]
  });
};

/**
 * 微信获取位置
 */
export const handleGetLocation = config => {
  return new Promise((resolve, reject) => {
    wxconfigInfo(config);
    wx.ready(function() {
      wx.getLocation({
        type: "wgs84", // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
        success: function(res) {
          console.warn("微信sdk定位成功", res);
          resolve({
            lat: res.latitude, // 纬度
            lng: res.longitude, // 经度
            speed: res.speed, // 速度，以米/每秒计
            accuracy: res.accuracy // 位置精度
          });
        },
        fail: function(err) {
          console.error("微信sdk定位失败", err);
          reject(err);
        }
      });
    });
    wx.error(function(err) {
      // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
      console.log("wxjsapi-error=", err);
      reject(err);
    });
  });
};

/**
 * 微信扫一扫
 */
export const handleScanQR = config => {
  return new Promise((resolve, reject) => {
    wxconfigInfo(config);
    wx.ready(function() {
      // config信息验证成功后会执行ready方法,所有接口调用都必须在config接口获得结果之后
      // config 是一个客户端的异步操作,所以如果需要在页面加载时调用相关接口,则须把相关接口放在ready函数中调用来确保正确执行.对于用户触发是才调用的接口,则可以直接调用,不需要放在ready函数中
      wx.checkJsApi({
        // 判断当前客户端版本是否支持指定JS接口
        jsApiList: ["scanQRCode"],
        success: function(res) {
          // 以键值对的形式返回，可用true，不可用false。如：{"checkResult":{"scanQRCode":true},"errMsg":"checkJsApi:ok"}
          if (res.checkResult.scanQRCode === true) {
            wx.scanQRCode({
              // 微信扫一扫接口
              desc: "scanQRCode desc",
              needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
              scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
              success: function(res) {
                console.warn("微信sdk定位成功", res);
                resolve({
                  str: res.resultStr //  当needResult 为 1 时，扫码返回的结果
                });
                var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                // location.href=res.resultStr;//扫描结果传递到的处理页面,跳转到这个页面
                // location.href=res.resultStr;//扫描结果传递到的处理页面,跳转到这个页面
                // sessionStorage.setItem('saomiao_result',result);
                //其它网页调用二维码扫描结果：
                // var result = sessionStorage.getItem("saomiao_result");
              }
            });
          } else {
            alert("抱歉，当前客户端版本不支持扫一扫");
          }
        },
        fail: function(err) {
          console.error("调用微信扫一扫失败", err);
          reject(err);
        }
      });
    });
    wx.error(function(err) {
      // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
      console.log("wxjsapi-error=", err);
      reject(err);
    });
  });
}

export default {handleGetLocation, handleScanQR}





