import Vue from 'vue'
import axios from '@/axios';
import Vant from 'vant';
import 'vant/lib/index.css';
import vueCookies from 'vue-cookies'
// 在main.js中引入 weixin-jsapi
import wx from "weixin-jsapi"; 
import {handleGetLocation, handleScanQR} from '@/utils/wxJsApiSdk'

import Scanqr from './Scanqr.vue'

// 在main.js中注册 weixin-jsapi
Vue.prototype.wx = wx

Vue.prototype.$handleGetLocation = handleGetLocation
Vue.prototype.$handleScanQR = handleScanQR

Vue.use(vueCookies);
Vue.use(Vant);

new Vue({
  render: h => h(Scanqr),
	axios
}).$mount('#app')



